<template>
  <div id="parser">
    <h3>PARSER</h3>

    <input type="file" @change="readFile" />
    <ul>
      <div v-for="(val,index) in valor" v-bind:key="index">
        <h4>{{index}}</h4>
        <div v-for="(v,index) in val" v-bind:key="index">
          <h5>{{index}} :</h5>
          <h6 v-if="typeof v != 'object'">{{v}}</h6>
          <div v-if="typeof v === 'object'">
            <h6 v-for="(v1,index) in v" v-bind:key="index">{{index}} : {{v1}}</h6>
          </div>
        </div>
      </div>
    </ul>
  </div>
</template>
<script>

import HL7 from "hl7-standard/src/api";

export default {
  name: "parser",
  data() {
    return {
      bandera: false,
      valor: {},
      campos: []
    };
  },
  methods: {
    readFile(ev) {
      let esto = this;
      let mensaje;
      let auxiliar;
      let x = 0;
      const file = ev.target.files[0];
      const reader = new FileReader();
      reader.readAsText(file);
      reader.onload = function(e) {
        let hl7 = new HL7(reader.result);
        hl7.transform(err => {
          if (err) throw err;
        });
        let m = hl7.getSegments();
        $.each(m, function(index, value) {
          esto.valor[value.type] = value.data;
          esto.$forceUpdate();
         let x = new HL7() 
          x.createSegment("MSH");
          x.set("MSH", {
            "MSH.2": "^~\\&",
            "MSH.3": "Example",
            "MSH.4": "123456",
            "MSH.5": "",
            "MSH.6": "",
            "MSH.7": "12121",
            "MSH.8": "",
            "MSH.9": {
              "MSH.9.1": "ADT",
              "MSH.9.2": "A08"
            },
            "MSH.10": "",
            "MSH.11": "T",
            "MSH.12": "2.3"
          });
          console.log(x.build())
        });

        console.log(esto.valor);
        console.log(hl7.get("PID"));
      };
    }
  }
};
</script>



