<template>
    <div id="register">
        <h3> Registo de pacientes </h3>
        <div class="row">
            <form @submit.prevent="saveEmpleado" class="col s12">
                
                 <!-- pid -->
                <div class="row">
                 <div class="input-field col s6">
                 <input id="first_name" type="text" v-model="Nombre" required>
                 <label for="first_name">Nombre</label>
                </div>
                <div class="input-field col s6">
                 <input id="last_name" type="text" v-model="Apellido" required>
                 <label for="last_name">Apellido</label>
                 </div>
                </div>
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Fecha" required> 
                        <label> Fecha de nacimiento (YY/mm/dd) </label>
                    </div>
                </div>
                <p>
                 <label>
                  <input class="with-gap" name="group1" type="radio"  />
                  <span>Femenino</span>
                 </label>
                </p>
                <p>
                 <label>
                  <input class="with-gap" name="group1" type="radio"  />
                  <span>Masculino</span>
                 </label>
                </p>

                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Direccion" required> 
                        <label> Dirección del paciente </label>
                    </div>
                </div>
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Telefono_casa" required> 
                        <label> Número de telefono de casa </label>
                    </div>
                </div>
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Estado_civil" required> 
                        <label> Estado Civil </label>
                    </div>
                </div>
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Etnicidad" required> 
                        <label> Etnicidad del paciente </label>
                    </div>
                </div>
                
                <!-- nk1 -->
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Nombre_familiar" required> 
                        <label> Nombre de familiar o parte asociada </label>
                    </div>
                </div>

                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Relacion_familiar" required> 
                        <label> Relación personal del familiar o asociado </label>
                    </div>
                </div>

                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Direccion_familiar" required> 
                        <label> Dirección del familiar o asociado </label>
                    </div>
                </div>
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Telefono_familiar" required> 
                        <label> Telefono del familiar o asociado </label>
                    </div>
                </div>
                <!-- al1 -->
                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Alergia" required> 
                        <label> Alergia del paciente </label>
                    </div>
                </div>

                <div class="row">
                    <div class ="input-field col s12">
                        <input type="text" v-model="Reaccion_alergia" required> 
                        <label> Reacción que produce la alergia en el paciente </label>
                    </div>
                </div>

                <button type="submit" class="btn green"> Submit </button>
                <router-link to="/" class="btn grey"> Cancel </router-link>
            </form>
        </div>
       
         
    </div>
</template>

<script>
import db from "./firebaseInit"
export default {
    name: 'register',
        data () {
        return {
            Nombre: null,
            Apellido: null,
            Fecha: null,
            Direccion: null,
            Telefono_casa: null,
            Estado_civil: null,
            Etnicidad: null,
            Nombre_familiar: null,
            Relacion_familiar: null,
            Direccion_familiar: null,
            Telefono_familiar: null,
            Alergia: null,
            Reaccion_alergia: null
            
        }
    },
    methods: {
        saveEmpleado (){
            db.collection('Empleados').add(
                {Nombre: this.Nombre,
                Apellido: this.Apellido,
                Fecha: this.Fecha,
                Direccion: this.Direccion,
                Telefono_casa: this.Telefono_casa,
                Estado_civil: this.Estado_civil,
                Etnicidad: this.Etnicidad,
                Nombre_familiar: this.Nombre_familiar,
                Relacion_familiar: this.Relacion_familiar,
                Direccion_familiar: this.Relacion_familiar,
                Telefono_familiar: this.Telefono_familiar,
                Alergia: this.Alergia,
                Reaccion_alergia: this.Reaccion_alergia})
                .then(docRef => {
                    this.$router.push('/')
                })
                .catch(error => console.log(error))
        }
    }
}
</script>


